.note-container {
  padding-bottom: 50px;
}
.note-container > h1 {
  width: 800px;
  padding-top: 60px;
  font-size: 36px;
  font-weight: 500;
  cursor: default;
  margin: 0 auto;
}
.note-container > h6 {
  width: 800px;
  font-weight: normal;
  cursor: default;
  color: rgba(0, 0, 0, 0.45);
  font-size: 14px;
  margin: 16px auto 28px;
}
.note-container > div {
  width: 100%;
}
.note-container > div img {
  display: block;
  margin: 0 auto 24px;
  max-width: 800px;
}
.note-container > div p {
  width: 800px;
  margin: 0 auto 14px;
  font-size: 16px;
  line-height: 1.8;
  color: rgba(0, 0, 0, 0.85);
}
.note-container > div h3 {
  width: 800px;
  margin: 0 auto 38px;
  border-bottom: 1px solid #eee;
  color: #000;
  font-weight: 500;
  font-size: 26px;
  padding: 14px 0 10px 0;
}
